<template>
  <div>
    <el-menu
      class="el_menu"
      mode="horizontal"
      text-color="#ffffff"
      active-text-color="#FA6A02"
      router
      background-color="#2B2A33"
      :default-active="this.$route.path"
    >
      <el-menu-item index="/Recommend">推荐</el-menu-item>
      <el-menu-item index="/Rank">排行榜</el-menu-item>
      <el-menu-item index="/Sheet">歌单</el-menu-item>
      <el-menu-item index="/Vedio">MV</el-menu-item>
      <el-menu-item index="/Singer">歌手</el-menu-item>
      <el-menu-item index="/MyMusic">我的音乐</el-menu-item>
    </el-menu>

    <el-main class="el_main">
      <router-view />
    </el-main>

    <aplayer
      mode="circulation"
      class="player"
      v-if="showfooter"
      autoplay
      :music="musicList[0]"
      :list="musicList"
      :listFolded="true"
      :mutex="true"
      :showLrc="true"
    >
    </aplayer>
  </div>
</template>

<script>
// 引入媒体播放器
import aplayer from 'vue-aplayer'
export default {
  components: {
    aplayer: aplayer
  },
  data () {
    return {
      // 多个音乐播放
      musicList: [],
      showfooter: false
    }
  },
  computed: {},
  watch: {},
  methods: {
    // 获取歌词
    async getLrc (id) {
      const { data: res } = await this.$http.get('lyric', {
        params: { id: id }
      })
      console.log(res)
      if (res.code === 200) {
        this.singlemusic.lrc = res.lrc.lyric
      }
    }
  },
  created () {
    let _this = this
    window.addEventListener('setItemEvent', function (e) {
      // 切换歌曲先把showfooter为false否则不起作用
      _this.showfooter = false
      _this.musicList = []
      if (e.key === 'tableData') {
        // 歌单
        var array = JSON.parse(e.newValue)
        for (var i = 0; i < array.length; i++) {
          _this.musicList.push({
            title: array[i].name,
            artist: array[i].singer,
            src:
              'https://music.163.com/song/media/outer/url?id=' +
              array[i].id +
              '.mp3',
            pic: array[i].url
          })
        }
        _this.showfooter = true
      } else if (e.key === 'singleMusic') {
        // 单曲
        _this.musicList.push({
          title: JSON.parse(e.newValue)['title'],
          artist: JSON.parse(e.newValue)['artist'],
          src: JSON.parse(e.newValue)['src'],
          pic: JSON.parse(e.newValue)['pic']
        })
        _this.showfooter = true

        // 获取歌词
        // _this.getLrc(JSON.parse(e.newValue)['id'])
      }
      console.log('当前歌曲列表', _this.musicList)
    })
  },
  mounted () {},
  destoryed () {}
}
</script>
<style lang="scss" scoped>
.el_menu {
  width: 100% height 50px;
}

.el_main {
  width: 100%;
  height: auto;
  margin-bottom: 100px;
}

.player {
  position: fixed;
  bottom: 0;
  width: 95%;
  height: auto;
  background: white;
}
</style>



